import { get } from 'request';
import config from './config';


/**
 * 
 */
class StatusFile {
	private file: string;

	private options: Array<String>;

	/**
	 * 
	 */
	constructor(){
		this.loadOptions();
	}

	/**
	 * 
	 */
	loadOptions(): void {
		get(config.statusUrl, (error: any, response:any, body:any) => {
			if (!error && response.statusCode == 200) {
		        console.log(body);
		    }
		});
	}
}

export { StatusFile }